import React from "react";
import "../styles/Bmi.scss";
import MuiButton from "../components/MuiButton";
import BmiHome from "../img/BmiHome.jpg";
import Vegetables from "../img/Vegetables.jpg";
import BmiCalc from "../components/BmiCalc";

function Bmi() {
  return (
    <div className="BmiPage">
      <div className="BmiHome">
        <div className="BmiPhoto">
          <img src={BmiHome} alt="Bmi slika" />
        </div>
        <div className="BmiText">
          <p></p>
          <p>
            Pita se da li ima vika kilograma? Izraunaj svoj stepen
            uhranjenosti sa naim Bmi kalkulatorom!
          </p>
          <h1>
            SAZNAJ <span className="SpanBmi">ODMAH</span>
          </h1>
          <MuiButton component={"OpisBmi"} />
        </div>
      </div>
      <div className="OpisBmi">
        <h2>Bmi</h2>
        <p>
          Indeks tjelesne mase (BMI) je jedan od&nbsp;
          <b>naina procjene uhranjenosti</b>. Vrijednosti preporuenog BMI-a
          iste su za oba spola, on iznosi od 18,5-24,9 kg/m2 prema klasifikaciji
          zdravstvene organizacije za europsko stanovnitvo. Ako su vae
          vrijednosti daleko ispod ili iznad prosjeka, svakako morate otii kod
          svog ljekara, kako biste smanjili rizik za vae zdravlje. to je vii
          iznad okvira urednih vrijednosti, to je vei rizik od oboljevanja od
          raznih sranih bolesti, dijabetesa i povienog krvnog pritiska.
        </p>
        <img src={Vegetables} alt="Bmi Slika" />
        <p>
          Sam indeks tjelesne mase <b>nije savrena mjera</b>, jer e vie ili
          miiavije osobe openito imati vii indeks tjelesne mase, premda e
          udio masnog tkiva biti relativno nizak, tako da je pri procjeni rizika
          od razvoja bolesti povezanih s pretilou vano uzeti u obzir i neke
          druge mjere. Izraunajte svoj BMI i otkrijte&nbsp;
          <b>va stepen uhranjenosti</b>.
        </p>
      </div>
      <BmiCalc />
    </div>
  );
}

export default Bmi;
