import React from "react";
import "../styles/Bmr.scss";
import BmrHome from "../img/BmrHome.jpg";
import MuiButton from "../components/MuiButton";
import Apples from "../img/Apples.jpg";
import BmrCalc from "../components/BmrCalc";

function Bmr() {
  return (
    <div className="BmrPage">
      <div className="BmrHome">
        <div className="BmrText">
          <h1>
            OSTVARITE SVOJE <span className="SpanBmr">CILJEVE</span>
          </h1>
          <p>
            Saznaj koliko tvoje tijelo troi kalorija u stanju mirovanja i lake
            doi do svog cilja!
          </p>
          <MuiButton component={"OpisBmr"} />
        </div>
        <div className="BmrPhoto">
          <img src={BmrHome} alt="Bmr slika" />
        </div>
      </div>
      <div className="OpisBmr">
        <h2>Bmr</h2>
        <p>
          Stopa bazalnog metabolizma (BMR) je mjera tjelesne potronje energije
          u mirovanju, te odreuje koliko kalorija trebate uzimati svaki dan za
          odravanje ili smanjenje tjelesne teine. Ovo je
          <b>
            &nbsp;broj kalorija koje tijelo treba za pravilno funkcioniranje
            osnovnih vitalnih funkcija
          </b>
          . U tom se stanju energija koristi samo za odravanje vitalnih organa.
          Openito, BMR se odreuje upravo radi smanjenja tjelesne teine. Za
          odravanje zdrave tjelesne teine potrebno je
          <b>&nbsp;uravnoteiti unos i potronju energije</b>. Jednom kada znate
          koliki je va BMR, odravanje ravnotee je vrlo jednostavno.
        </p>
        <img src={Apples} alt="Bmr slika" />
        <p>
          Jednom kada znate svoj BMR,
          <b>
            &nbsp;tano moete znati koliko kalorija sagorite tijekom dana u
            mirovanju
          </b>
          . Pomou svog BMR-a moete odrediti koliko kalorija trebate uzeti kako
          biste postigli eljenu miinu masu, izgubili masti ili odravali
          teinu. Jednostavno reeno, ako znate koliko kalorija unesete i koliko
          sagorite, put do vaeg cilja bit e laki. Pokrenite nas kalkulator i
          izraunajte va BMR!
        </p>
      </div>
      <BmrCalc />
    </div>
  );
}

export default Bmr;
