import React from "react";
import { NavLink } from "react-router-dom";
import "../styles/Kalorije.scss";
import HomePhoto from "../img/HomePhoto.jpg";
import MuiButton from "../components/MuiButton";
import KalorijeOpis from "../img/KalorijeOpis.jpg";
import KalorijeFood from "../img/KalorijeFood.jpg";

function Kalorije() {
  return (
    <div className="HomePage">
      <div className="Home">
        <div className="HomeText">
          <h1>
            SAZNAJ SVE O KALORIJAMA<br></br>I ZDRAVOM IVOTU
          </h1>
          <p>
            Pokrenite <span className="darkText">NAE KALKULATORE</span> i
            <br></br> saznajte sve potrebno da
            <br></br>
            dovedete vae tijelo u <br></br> eljenu formu
          </p>
          <MuiButton component={"Opis"} />
        </div>
        <div className="HomePhoto">
          <img src={HomePhoto} alt="HomePhoto" />
        </div>
      </div>
      <div className="Opis">
        <h2>Sta su Kalorije?</h2>
        <p>
          Kada govorimo o prehrani, kalorije su energija osloboena iz hrane
          tijekom procesa razgradnje. Sva energija koja je potrebna naem
          organizmu, osigurava se unosom hrane, a broj kalorija u hrani pokazuje
          koliko potencijalne energije hrana posjeduje. Kalorija u hrani je
          zapravo kilokalorija ili 1000 kalorija, ali se u uobiajenom govoru
          koristi samo kalorija umjesto kilokalorija.
          <b>
            &nbsp;Koliko dnevno trebate unijeti kalorija ovisi o veliini vaeg
            tijela i o razini dnevne aktivnosti
          </b>
          . Da provjerite koliko vase tijelo trosi kalorija dnevno u stanju
          mirovanja pokrenite nas
          <NavLink to="/Bmr" className="textLink">
            &nbsp;Bmr&nbsp;
          </NavLink>
          kalkulator.
        </p>
        <img src={KalorijeOpis} alt="Apples" />
        <p>
          Za enu prosjene teine i prosjene aktivnosti dnevna potronja
          kalorija je oko 2000 do 2500 kcal. Za mukarca prosjene teine i
          prosjene aktivnosti dnevna potronja kalorija je 2500 do 3000. Na
          etiketama nutritivnih vrijednosti mnogih proizvoda moete primjetiti
          nekoliko glavnih parametara koji se uvijek istiu, a to su{" "}
          <b>proteini, masti i ugljeni hidrati</b>. Ove tri stavke su glavni
          izvori energije. Ostali parametri kao to su voda, vlakna, nesvarljive
          tvari, minerali i vitamini ne uestvuju u stvaranju energije. Svaka
          namirnica ili jelo ima odreenu kombinaciju, uglavnom ova tri prva
          izvora i daju odreeni broj kalorija, odnosno odreenu koliinu
          energije.
        </p>
      </div>
      <div className="Ishrana">
        <h2>Ishrana</h2>
        <p>
          Koliko god se trudili da potroite sve unesene kalorije, mnogo je
          bitnije <b>porijeklo tih kalorija</b>. Ono to treba izbjegavati su
          prazne kalorije. Prazne kalorije su one koje pruaju energiju, ali
          jako malo <b>nutritivne vrijednosti</b>. Zato izbjegavajte sve vrste
          dodatih eera i masti ukoliko ih vidite na deklaraciji proizvoda.
          Uglavnom ne sadre vlakna, amino kiseline, antioksidanse, hranljive
          minerale ili vitamine. Njih unosimo uglavnom konzumirajuci{" "}
          <b>eer, nezdrave masti i alkohol</b>. Zbog svega navedenog lahko je
          shvatiti da su prazne kalorije glavni uzrok gojaznosti u modernom
          dobu. Jedan od naina procjene uhranjenosti je
          <NavLink to="/Bmi" className="textLink">
            &nbsp;Bmi&nbsp;
          </NavLink>
          kalkulator.
        </p>
        <img src={KalorijeFood} alt="Vegetables" />
        <p>
          Zdrava ishrana je jedan od preduslova{" "}
          <b>uspjenog i zdravog mravljena</b>, svaka druga zdrava aktivnost
          nee dati rezultate ukoliko paralelno sa njima ne vodite rauna o tome
          ta jedete. Provjerite koliko koja
          <NavLink to="/Aktivnosti" className="textLink">
            &nbsp;aktivnost&nbsp;
          </NavLink>
          trosi kalorija ali ono to donosi najbolje rezultate mravljenja,
          jeste <b>kombinacija zdrave ishrane i treniranja</b>. Jedite puno voa
          i povra, proteina, cjelovitih itarica i zdravih masti. Isto tako
          unoenje mnogo vode u organizam vaan je dio odravanja optimalne
          tjelesne teine i prehrane bogate hranjivim tvarima.{" "}
          <b>Zdrava ishrana je baza zdravog ivota!</b>
        </p>
      </div>
    </div>
  );
}
export default Kalorije;
