import React from "react";
import "../styles/Aktivnosti.scss";
import Trening from "../img/Trening.jpg";
import AktivnostiSlika from "../img/AktivnostiSlika.jpg";
import MuiButton from "../components/MuiButton";
import AktivnostiCalc from "../components/AktivnostiCalc";

function Aktivnosti() {
  return (
    <div className="AktivnostiPage">
      <div className="AktivnostiHome">
        <div className="AktivnostiPhoto">
          <img src={AktivnostiSlika} alt="Aktivnosti slika" />
        </div>
        <div className="AktivnostiText">
          <p>
            Oduvijek te zanimalo koliko potroi kalorija u svojim aktivnostima?
          </p>
          <h1>
            POKRENI NA <span className="SpanAkt">KALKULATOR</span> I ISPLANIRAJ
            SVOJ TRENING
          </h1>
          <MuiButton component={"OpisAktivnosti"} />
        </div>
      </div>
      <div className="OpisAktivnosti">
        <h2>Aktivnosti</h2>
        <p>
          Tjelesna aktivnost, zdravlje i kvaliteta ivota su blisko povezani.
          Aktivan ivot je u izravnoj vezi s brojnim socijalnim i psiholokim
          koristima, a povezan je i s produenim oekivanim trajanjem ivota
          zbog ega osobe koje se <b>redovito bave tjelesnom aktivnou</b> ive
          due. Osobe koje su sjedalaki ivotni stil zamijenili aktivnim,
          iskazuju zadovoljstvo tjelesnim i psihikim zdravljem i{" "}
          <b>boljom kvalitetom ivota</b>. Sjedalaki nain ivota je razlog za
          razvoj brojnih kroninih bolesti, ukljuujui bolesti srca i krvnih
          ila.
        </p>
        <img src={Trening} alt="Trening slika" />
        <p>
          Strunjaci preporuuju svakodnevno 60 ili vie minuta umjerene do
          intenzivne tjelesne aktivnosti. Svaki trening ima tono odreeni
          metaboliki ekvivalent aktivnosti, koji u kombinaciji s tvojom
          tjelesnom teinom i vremenskim rasponom treninga ini jednainu pomou
          koje <b>moe saznati potronju kalorija</b>. Pokreni na kalkulator i
          saznaj koliko kalorija potrosis!
        </p>
      </div>
      <AktivnostiCalc />
    </div>
  );
}

export default Aktivnosti;
